<template>
  <v-container>
    <v-row>
      <v-col>
        <h1>{{ count }}</h1>
        <h3>{{ $store.getters.doubleCount }}</h3>
        <v-btn @click="decrement">-</v-btn>
        <v-btn @click="increment">+</v-btn>
        <v-list-item v-for="(cat, i) in categories" :key="i">
          {{ cat }}</v-list-item
        >
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  name: "About",
  data() {
    return {};
  },
  computed: mapState(["count", "categories"]),
  ...mapGetters(["doubleCount"]),
  methods: {
    increment() {
      this.$store.dispatch("incrementAsync");
    },
    decrement() {
      this.$store.commit("decrement");
    },
  },
};
</script>
